#Footer-container{
    background-color: #13435b;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Footer-h2{
    font-family: "Poiret One",serif;
    font-weight: lighter;
    color: white;
}

#Footer-UAAsso{
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 10px;
    align-items: center;
}

#Footer-Contacts{
    display: grid;
    grid-gap: 5px;
    align-items: center;
    grid-template-columns: auto auto auto auto;
}

#FooterMail-a{
    display: grid;
    align-items: center;
    grid-template-columns: auto auto;
    grid-gap: 5px;
    text-decoration: none
}

@media only screen and (max-width: 767px) {
    #Footer-container{
        height: 40px;
        padding-top: 5px;
        padding-bottom: 5px}
    #Footer-UALogo{height: 30px; padding: 2px}
    .Footer-RSlogos{height: 10px;}
    .Footer-h2{
        font-size: 10px;
        margin-left: 0;
        margin-right: 3px;}
}

@media only screen and (min-width: 768px) {
    #Footer-container{
        height: 50px;
        padding-top: 5px;
        padding-bottom: 5px}
    #Footer-UALogo{height: 40px;}
    .Footer-RSlogos{height: 15px;}
    .Footer-h2{
        font-size: 20px;
        margin-left: 5px;
        margin-right: 5px;}
}