@import url(https://fonts.googleapis.com/css2?family=Poiret+One&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.AppSiteUA{
    width: 100vw;
    overflow: hidden;
}

.Pages{
    background-color: white;
    display: grid;
    grid-template-areas: "a b c";
}

.LeftColumn, .RightColumn{
    background-color: #f2f2f2;
    position: relative;
    z-index: 0;
}
.LeftColumn{grid-area: a;}
.MiddleColumn{grid-area: b; position: relative; z-index: 5}
.RightColumn{grid-area: c;}

#ulMenuNavigation {
    list-style: none;
    background-color: rgba(19, 67, 91, 0);
    position: fixed;
    z-index: 10;
    transition-property: background-color;
    transition-duration: 0.5s;
}

#liMenuNavigationIDlogo {
    grid-area: a;
    transition-property: opacity;
    transition-duration: 0.5s;
}
#liMenuNavigationID {grid-area: c;}
#liMenuNavigationIDVieAssociative{grid-area: d;}
#liMenuNavigationIDManifestationAssociative{grid-area: e;}

.liMenuNavigation a {
    text-decoration: none;
    color: white;
    font-family: "Poiret One", serif;
}

@media only screen and (max-width: 767px) {
    .emptyImage{
        width: 100vw;
        margin-bottom: -5vh;
    }

    #BurgerButtonMenu{
        position: fixed;
        top: 17px;
        right: 17px;
        z-index: 11;
    }

    .BarBurgerButtonMenu{
        background-color: white;
        height: 3px;
        margin-bottom: 4px;
        width: 20px;
    }

    #uaLogoMenu{
        height: 30px;
        margin: 10px;
        opacity: 0;
        transition-property: opacity;
        transition-duration: 0.5s;
    }

    .Pages{
        grid-template-columns: 10vw 80vw 10vw;
    }

    #MobileHorizontalBarMenu{
        position: fixed;
        z-index: 11;
        top:0;
        left:0;
        background-color: rgba(19, 67, 91, 0);
        width: 100vw;
        height: 50px;
        transition-property: background-color;
        transition-duration: 0.5s;
    }

    #ulMenuNavigation {
        position: fixed;
        z-index: 11;
        top:50px;
        right: 0;
        border-top-color: rgba(255, 255, 255, 0.7);
        border-top-width: 1px;
        border-top-style: solid;
        margin-top:0;
        background-color: rgba(19, 67, 91, 0.7);
        padding: 10px;
    }

    .MiddleColumn{margin-bottom: 20px;}
}

@media only screen and (min-width: 768px) {
    .emptyImage{
        width: 100vw;
        margin-bottom: -15vh;
    }

    #uaLogoMenu{
        height: 50px;
    }

    #ulMenuNavigation {
        display: grid;
        grid-template-areas: "a b c d e";
        grid-template-columns: auto 150px auto auto auto;
        grid-gap: 50px;
        justify-content: center;
        justify-items: center;
        align-items: center;
        padding: 10px;
        width: 100vw;
        height: 50px;
        top:-18px;
        left:0;
    }

    .Pages{
        grid-template-columns: 19vw 62vw 19vw;
    }

    #liMenuNavigationIDlogo {
        transition-property: opacity;
        transition-duration: 0.5s;}

    .MiddleColumn{margin-bottom: 50px;}
}

/* width */
::-webkit-scrollbar {
    display: none;

}
#SplashPage-h1{
    font-family: "Poiret One", serif;
    color: white;
    text-align: center;
}

#SplashPage-Container{
    background-color: rgba(19, 67, 91, 1);
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    justify-items: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}
#Footer-container{
    background-color: #13435b;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Footer-h2{
    font-family: "Poiret One",serif;
    font-weight: lighter;
    color: white;
}

#Footer-UAAsso{
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 10px;
    align-items: center;
}

#Footer-Contacts{
    display: grid;
    grid-gap: 5px;
    align-items: center;
    grid-template-columns: auto auto auto auto;
}

#FooterMail-a{
    display: grid;
    align-items: center;
    grid-template-columns: auto auto;
    grid-gap: 5px;
    text-decoration: none
}

@media only screen and (max-width: 767px) {
    #Footer-container{
        height: 40px;
        padding-top: 5px;
        padding-bottom: 5px}
    #Footer-UALogo{height: 30px; padding: 2px}
    .Footer-RSlogos{height: 10px;}
    .Footer-h2{
        font-size: 10px;
        margin-left: 0;
        margin-right: 3px;}
}

@media only screen and (min-width: 768px) {
    #Footer-container{
        height: 50px;
        padding-top: 5px;
        padding-bottom: 5px}
    #Footer-UALogo{height: 40px;}
    .Footer-RSlogos{height: 15px;}
    .Footer-h2{
        font-size: 20px;
        margin-left: 5px;
        margin-right: 5px;}
}
