@import url('https://fonts.googleapis.com/css2?family=Poiret+One&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

.AppSiteUA{
    width: 100vw;
    overflow: hidden;
}

.Pages{
    background-color: white;
    display: grid;
    grid-template-areas: "a b c";
}

.LeftColumn, .RightColumn{
    background-color: #f2f2f2;
    position: relative;
    z-index: 0;
}
.LeftColumn{grid-area: a;}
.MiddleColumn{grid-area: b; position: relative; z-index: 5}
.RightColumn{grid-area: c;}

#ulMenuNavigation {
    list-style: none;
    background-color: rgba(19, 67, 91, 0);
    position: fixed;
    z-index: 10;
    transition-property: background-color;
    transition-duration: 0.5s;
}

#liMenuNavigationIDlogo {
    grid-area: a;
    transition-property: opacity;
    transition-duration: 0.5s;
}
#liMenuNavigationID {grid-area: c;}
#liMenuNavigationIDVieAssociative{grid-area: d;}
#liMenuNavigationIDManifestationAssociative{grid-area: e;}

.liMenuNavigation a {
    text-decoration: none;
    color: white;
    font-family: "Poiret One", serif;
}

@media only screen and (max-width: 767px) {
    .emptyImage{
        width: 100vw;
        margin-bottom: -5vh;
    }

    #BurgerButtonMenu{
        position: fixed;
        top: 17px;
        right: 17px;
        z-index: 11;
    }

    .BarBurgerButtonMenu{
        background-color: white;
        height: 3px;
        margin-bottom: 4px;
        width: 20px;
    }

    #uaLogoMenu{
        height: 30px;
        margin: 10px;
        opacity: 0;
        transition-property: opacity;
        transition-duration: 0.5s;
    }

    .Pages{
        grid-template-columns: 10vw 80vw 10vw;
    }

    #MobileHorizontalBarMenu{
        position: fixed;
        z-index: 11;
        top:0;
        left:0;
        background-color: rgba(19, 67, 91, 0);
        width: 100vw;
        height: 50px;
        transition-property: background-color;
        transition-duration: 0.5s;
    }

    #ulMenuNavigation {
        position: fixed;
        z-index: 11;
        top:50px;
        right: 0;
        border-top-color: rgba(255, 255, 255, 0.7);
        border-top-width: 1px;
        border-top-style: solid;
        margin-top:0;
        background-color: rgba(19, 67, 91, 0.7);
        padding: 10px;
    }

    .MiddleColumn{margin-bottom: 20px;}
}

@media only screen and (min-width: 768px) {
    .emptyImage{
        width: 100vw;
        margin-bottom: -15vh;
    }

    #uaLogoMenu{
        height: 50px;
    }

    #ulMenuNavigation {
        display: grid;
        grid-template-areas: "a b c d e";
        grid-template-columns: auto 150px auto auto auto;
        grid-gap: 50px;
        justify-content: center;
        justify-items: center;
        align-items: center;
        padding: 10px;
        width: 100vw;
        height: 50px;
        top:-18px;
        left:0;
    }

    .Pages{
        grid-template-columns: 19vw 62vw 19vw;
    }

    #liMenuNavigationIDlogo {
        transition-property: opacity;
        transition-duration: 0.5s;}

    .MiddleColumn{margin-bottom: 50px;}
}

/* width */
::-webkit-scrollbar {
    display: none;

}